import * as React from "react"
import Header from '../components/Header'
import Layout from '../components/Layout'
import Helmet from '../components/Helmet'
import Transition from '../components/Transition'
import { Link } from "gatsby"
import dotsw from '../../static/dotsw.png'
import dotsb from '../../static/dotsb.png'
import bg from '../../static/dotbg.png'
import favicon from '../../static/favicon.png'

import $ from 'jquery';
import { faArrowAltCircleRight, faArrowDown, faArrowRight, faBriefcase, faUser } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { useState, useEffect } from 'react';



function view(){
  document.getElementById("hi").scrollIntoView({behavior:'smooth'});
}

export default function Home() {
  const [showTransition, setShowTransition] = useState(false);

  useEffect(() => {
    // Check if navigation is forward (not back)
    const navigationType = window.performance.getEntriesByType('navigation')[0]?.type;
    if (navigationType === 'navigate' || navigationType === 'reload') {
      setShowTransition(true); // Show animation only on forward or reload
      const timer = setTimeout(() => setShowTransition(false), 1000);
      return () => clearTimeout(timer);
    }
  }, []);


  if(typeof window !== 'undefined'){
  window.addEventListener("scroll",function(){
    var pageBottom = window.scrollY + window.screen.height;
    var tags = $(".appear");
    var elements = document.getElementsByClassName("appear");

    for (var i = 0; i < tags.length; i++) {

      var tag = tags[i];

    if (elements[i].offsetTop < pageBottom) 
      {
        $(tag).addClass("visible");
      } 
      else
      {
        $(tag).removeClass("visible");

      }
    }

  });
  return(
  <div>
{showTransition && <Transition />}    
<Header>
   <Helmet></Helmet>
    <title>Anindya Biswas - Home</title>
    <div className="flex flex-wrap items-center align-middle justify-evenly h-dvh dark:bg-neutral-800 bg-neutral-200 duration-500">
      <img src={dotsw} id="bg" alt="dots" className=" absolute object-cover h-full rotate max-sm:h-2/3 opacity-60 hidden visible dark:block"/>
      <img src={dotsb} id="bg" alt="dots" className=" absolute object-cover h-full rotate max-sm:h-2/3 opacity-60  dark:hidden"/>
      <h1 className="text-5xl max-sm:text-2xl py-4  dark:bg-neutral-800 bg-neutral-200 text-neutral-400 w-full text-center fade-up z-0 duration-0">
        Welcome to&nbsp;
        <Link className=" hover:text-red-600 rounded-full hover:px-4 hover:pb-1 text-white  duration-500">Anindya's</Link> Home!</h1>
        <div className="bg-red-600 text-white font-bold px-6 py-3 rounded-full absolute bottom-14 lg:right-14 hover:bg-red-500 duration-500 z-1" onClick={view}><FontAwesomeIcon icon={faArrowDown}/></div>

    </div>


    <div className="flex flex-wrap items-center align-middle justify-evenly bg-gray-50 dark:bg-neutral-900 min-h-screen max-h-fit">
      
      <div className=" p-10 pb-1 rounded-3xl appear dark:text-neutral-200" id="slide2">
        <h1 className=" text-6xl mb-5" id="hi">Hi there! üòÅ</h1>
        <p className="mb-2 ms-2 opacity-70"> It's Anindya Biswas and this is my online home.</p>
        <p className="mb-1 ms-2 opacity-70"> I'm living my life, doing my masters in AI and making fun projects...</p>
        <p className="mb-14 ms-2 opacity-70">Go to About to know more about me and also checkout my Works.</p>
        <div className="flex flex-wrap items-center align-middle">

        <Link to="/works" className=" text-2xl rounded-full py-4 px-7 m-2 text-red-100 bg-red-600 hover:bg-red-500 hover:ease-in-out duration-300">Works <FontAwesomeIcon icon={faArrowRight} size="xs" className=" ms-2"/></Link>
        <Link to="/about" className=" text-2xl rounded-full py-4 px-7 m-2 text-red-950 hover:text-red-100 border border-red-600 hover:bg-red-600 hover:ease-in-out duration-300 dark:text-red-100">About <FontAwesomeIcon icon={faArrowRight} size="xs" className=" ms-2"/></Link>
        </div>
      </div>
    </div>

    </Header>
    </div>
  )

}
}

